import { NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';

import { SharedModule } from '../../../shared/modules/shared.module';
// import { ProductCategorySharedModule } from '../../../shared/modules/product_category_shared.module';

import { ProductCategoryMasterRoutingModule } from './product-category-master-routing.module';
import { ProductCategoryMasterComponent } from './product-category-master.component';
import { ProductCategoryAddComponent } from './product-category-add/product-category-add.component';
import { ProductCategoryEditComponent } from './product-category-edit/product-category-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ProductCategoryMasterRoutingModule,
    // ProductCategorySharedModule
  ],
  declarations: [
    ProductCategoryMasterComponent,
    ProductCategoryEditComponent,
    // ProductCategoryAddComponent
  ],
  entryComponents: [
    // ProductCategoryAddComponent,
    ProductCategoryEditComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class ProductCategoryMasterModule { }



