import { Component, OnInit, Input, Output, EventEmitter, Inject, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { Observable } from 'rxjs';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { ProductCategoryMasterService, ProductCategory, CurrentUserService ,ErrorHandleService} from '../../../../shared';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-product-category-edit',
  templateUrl: './product-category-edit.component.html',
  styleUrls: ['./product-category-edit.component.scss']
})
export class ProductCategoryEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('categoryCombo') categoryCombo: jqxComboBoxComponent;
  @ViewChild('validationCombo') validationCombo: jqxComboBoxComponent;

  @Output() productCategoryUpdated = new EventEmitter();
  @Input('productCategoryData') productCategoryData;
  @Output() openCategoryAdd = new EventEmitter();

  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
    pCForm: FormGroup;
    post:any;
    formData:any;
    catId:number;
    apiUrl: string;
    catLoader: boolean = false;
    categorysource: any;
    categoryAdapter: any;

    userData: any = {};

    validationAdapter:any = [
      {value:'PERIODIC'},
      {value:'PERPETUAL'}
    ];

    constructor(
      private fb: FormBuilder,
      private pcms: ProductCategoryMasterService,
      private ehs: ErrorHandleService,
      private cus: CurrentUserService,
      @Inject('API_URL') apiUrl: string, 
    ) { 
      this.pCForm = fb.group({
        'code': [null, Validators.required],
        'name': [null, Validators.required]
      });
      this.userData = this.cus.getTokenData();
      this.apiUrl = apiUrl;

    }
  
    ngOnInit() {
      // console.log(this.productCategoryData);
      this.catId = this.productCategoryData['id'];
      this.formData=new ProductCategory(this.productCategoryData);
      this.catLoader = true;
      // this.setCategoryCombo();
      this.pCForm.setValue(this.formData);
    }

    ngAfterViewInit(){
      if(this.productCategoryData && this.productCategoryData.valuation_method){
        this.validationCombo.selectItem(this.productCategoryData.valuation_method);
      }
    }
  
    updateProductCategory(value){
      // if( this.categoryCombo.getSelectedItem()==null){
      //   value['parent_id']=null;
      // }else{
      //   value['parent_id'] = this.categoryCombo.getSelectedItem().value;
      //   console.log(value);
      // }
      value['valuation_method'] = this.validationCombo.val() || null;
      if (value['valuation_method']) {
        this.jqxLoader.open();
        this.pcms.update(this.catId, value)
        .subscribe(
          result => {
            let data = this.ehs.formatError(result);
            if (data['result'] == true) {
              this.productCategoryUpdated.emit(data);
            } else {
              let messageDiv: any = document.getElementById('windowMessage');
              messageDiv.innerHTML = '';
              for (let i = 0; i < data['message'].length; i++) {
                let errorDiv = document.createElement('li');
                errorDiv.innerText = data['message'][i];
                messageDiv.appendChild(errorDiv);
              }
              this.winNotification.open();
            }
            this.jqxLoader.close();
          },
          error => {
            let data = this.ehs.formatError(error);
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerText = data['message'];
            this.winNotification.open();
            this.jqxLoader.close();
          }
        )
      }else{
        let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = 'Please Select Validation Method';
          this.winNotification.open();
      }
  
    }
  
    clearForm(): void {
      this.pCForm.reset();
    }

    // setCategoryCombo() {
    //   this.categorysource =
    //   {
    //       datatype: "json",
    //       contentType: "application/json; charset=utf-8",
    //       datafields: [
    //           { name: 'id'},
    //           { name: 'code'},
    //         ],
    //       id: 'id',
    //       data: this.userData,
    //       // async: false,
    //       url: this.apiUrl+'product_category?limit=all'
    //   };
    //   this.categoryAdapter = new jqx.dataAdapter(this.categorysource, {
    //     autoBind: true,
    //     downloadComplete: (data, status, xhr) => {
    //         let tData = data.data;
    //         this.catLoader = false;
    //         // this.jqxLoader.close();
    //         for (var _i = 0; _i < tData.length; _i++) {
    //           tData[_i]['code'] = tData[_i]['code'] + ' - ' + tData[_i]['name'];
    //         }
    //         // console.log(tData);
    //         return tData;
    //     },
    //     loadComplete: () => {
    //       this.categoryCombo.selectItem(this.productCategoryData.parent_id);
    //     },
    //     bindigComplete: () => {

    //     }
    //   });
    // }
      // addCategory(){
      //   this.openCategoryAdd.emit('open');
      // }
  }








