import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductCategoryMasterComponent } from './product-category-master.component';
import { CanNavigateRouteGuard } from '../../../shared';

const routes: Routes = [
    {
        path: '', 
        component: ProductCategoryMasterComponent,
        canDeactivate: [CanNavigateRouteGuard],
        // children: [
        //     // { path: 'dashboard', loadChildren: './dashboard/dashboard.module#DashboardModule' },
        // ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class ProductCategoryMasterRoutingModule { }



